 export default function anchor(lastLocation, currentLocation, active, callback) {//  ,   , ,    
    if (active) { //    
        window.location.href = currentLocation;
    }
    window.addEventListener('hashchange', (e) => { // url 
        console.log(window.location.href.indexOf(lastLocation))
        if (window.location.href[window.location.href.length - 1] == lastLocation[lastLocation.length-1] && active) { //    url     url....
            callback();
        }
    })
}